* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.footer {
  display: flex;
  width: 100%;
  height: 80vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.foterbox {
  display: flex;
  align-items: center;
  gap: 350px;
  width: 100%;
  height: 20vh;
  justify-content: center;
}

.fotertype {
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 10px;
}

.foterbutton button {
  width: 163px;
  height: 58px;
  color: white;
  background-color: #ffa62b;
  border-radius: 10px;
  border: #ffa62b;
  font-size: 1.1rem;
}

.fotercard {
  width: 100%;
  height: 352px;
  background-color: #16697a;
 
}

.fotercard-type{
 display: flex;
 align-items: center;
 justify-content: center;
 width: 100%;
 height: 100%;
 gap: 70px;
}

.foter-card-type {
  display: flex;
  flex-direction: column;
  text-align: left;
  color: white;
  gap: 15px;
}

.foter-card-type2{
  display: flex;
  flex-direction: column;
  text-align: left;
  color: white;
  gap: 15px;
}


.icon-type-foter img{
 width: 24px;
 height: 24px;
}

.icon-type-foter{
 display: flex;
 text-align: center;
}

.foter-bottom{
 display: flex;
 text-align: center;
 justify-content: space-around;
 width: 100%;
 gap: 500px;
 align-items: center;
 height: 10vh;
}

.bottom-icon-foter{
 display: flex;
 gap: 10px;
}

.foter-bottom img{
 width: 24px;
 height: 24px;
}