@media (max-width:400px ) {
    .practiv{
     display: flex;
     flex-direction: column;
     align-items: center;
     flex-wrap: wrap;
     width: 100%;
     height: 200vh;
    }

    .bottom-type{
     display: flex;
     flex-direction: column;
     width: 240px;
    }

    .box-bottom{
     display: flex;
     flex-direction: column;
     flex-wrap: wrap;
    }

    .bottom-card{
     display: flex;
     flex-direction: column;
     flex-wrap: wrap;
     align-items: center;
    }
}