@media (max-width:420px) {
 .Navbarbox .menu {
  display: none;
 }

 .Navbarbox .menu2{
  display: block;
  
}


 .Navbarbox .mobile-menu{
  display: flex;
  align-items: center;
  width: 340px;
  background-color: white;
  flex-direction: column;
  margin-top: 40px;
  right: 1px;
  list-style: none;
  animation-name: menu2;
  animation-duration: 1s;
  animation-timing-function: ease;
 }

 .menu2 .icon{
  position: absolute;
  right: 20px;
  top: 10px;
}
 
.Navbarbox .logo{
  position: absolute;
  top: 10px;
  left: 20px;
  font-size: 1.5rem;
}

 
 .Navbarbox .mobile-menu li {
  padding: 10px 20px;
  color: black;
  font-size: 1.3rem;
  border-radius: 5px;
 }
 
 .Navbarbox .none {
  display: flex;
  align-items: center;
  width: 100%;
  background-color: white;
  flex-direction: column;
  color: black;
  position: absolute;
  right: 1px;
  margin-top: 40px;
  list-style: none;
  transform: translateX(-100%);
  animation-name: menu3;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-iteration-count: 1;

}
  
   
 }
 .Navbarbox .none li {
  padding: 10px 20px;
  font-size: 18px;
  border-radius: 5px;
 }
 
 .Navbarbox .icon{
  display: block;
  padding-left: 100px;
  
 }
 

 @keyframes menu2 {
 from {
  transform: translateX(-100%);
}
to {
  transform: translateX(0%);
}
 
} 


 @keyframes menu3 {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(-120%);
  }
} 

 







