@media (max-width: 410px) {
  .footer {
    height: 200vh;
    display: flex;
    flex-direction: column;
  }

  .foterbox {
    width: 400px;
    height: 500px;
    display: flex;
    flex-direction: column;
  }

  .fotertype {
    width: 220px;
    display: flex;
  }

  .foterbutton {
    margin-top: 25px;
  }

  .fotercard {
    display: flex;
    flex-direction: column;
    width: 400px;
    height: 150vh;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 25px;
  }

  .fotercard-type {
    width: 400px;
    flex-direction: column;
  }

  .foter-card-type2{
    display: flex;
    width: 440px;
    align-items: center;
  }


}
