@media (max-width:420px){
 .busines-box{
  width: 400px;
  height: 220vh;
  
 }
 
 .busines-typee {
  flex-direction: column;
  width: 250px;
  height: 261px;
 margin-left:75px;
 text-align: left;
 }
 
 .business-content{
 display: flex;
 flex-direction: column;
 gap: 30px;
 }
 
 .busines-img{
  width: 333px;
  height: 500px;
  display: flex;
  padding-top: 105px;
 }
 
 .busines-img img{
  width: 100%;
  height: 100%;
 }
 
 .bus-icon{
  display: flex;
  flex-direction: column;
  margin-top: 55px;
 }



 .buttonbox{
   width: 370px;
   height: 220px;
   display: flex;
   flex-direction: column;
   flex-wrap: wrap;
 }
 


 .type-input{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 370px;
  height: 220px;
  flex-wrap: wrap;
 }

 .type-input input{
  width: 240px;
  height: 58px;
 }

 .input-button-busines button{
  width: 80px;
  height: 58px;
 }
}