@media   (max-width: 420px) {
 
  .Navbarbox{
    display: flex;
    flex-direction: column;
    width: 320px;
    height: 1000px;
    gap: 25px;
  }

  .box {
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }


  .card .type{
    width: 280px;
    display: flex;
    text-align: left;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
  }

  .button {
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
  }

  .rasm {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }

  .rasm img {
    width: 100%;
    height: 100%;
  }

  
  
  
}


